import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../views/Main.vue'
import StudyNote from '../views/StudyNote.vue'
import MyLife from '../views/MyLife.vue'
import Resume from '../views/Resume.vue'
import Login from '../views/Login.vue'
import Game from '../views/Game.vue'

Vue.use(VueRouter)

// define my routes, will be used later 
const routes = [
    {
        path:'/',
        name:'Main',
        component: Main,
        meta: {
            keepAlive: true
        }
    },
    {
        path:'/StudyNote',
        name:'StudyNote',
        component: StudyNote,
        meta: {
            keepAlive: true
        }
    },
    {
        path:'/MyLife',
        name:'MyLife',
        component: MyLife,
        meta: {
            keepAlive: true
        }
    },
    {
        path:'/Resume',
        name:'Resume',
        component: Resume,
        meta: {
            keepAlive: true
        }
    },
    {
        path:'/Login',
        name:'Login',
        component: Login,
        meta: {
            keepAlive: true
        }
    },
    {
        path:'/Game',
        name:'Game',
        component: Game,
        meta: {
            keepAlive: true
        }
    },
]
const router = new VueRouter({
    base: process.env.BABEL_ENV,
    // using 'routes' here, which is defined bofore
    routes: routes,
    // mode:'history'
})

export default router