<template>
<div id='Main'>
    <div class='header' id='mainHeader'>
        <h1>Train Twister</h1>
    </div>
    <div id="trackDiagram">
        <button id="firstSwitch" v-on:click="firstSwitch()">open/close</button>
        <button id="secondSwitch" v-on:click="secondSwitch()">open/close</button>
        <button id="startMove" v-on:click="startMove()">START</button>
        <button id="Remove" v-on:click="Remove()" title="Remove all the trains">REMOVE</button>
        <img :src="trackDiagramChoose" @mousemove="move">
        <p>X: {{trainX}} Y: {{trainY}}</p>
    </div>
</div>
</template>

<script>
import cc from "../assets/cc.png"
import cdc from "../assets/cdc.png"
import dcc from "../assets/dcc.png"
import dcdc from "../assets/dcdc.png"
export default {
    name: 'MyLife',
    data(){
        return{
            trackDiagramChoose:cc,
            isFirstTrackC:true, // the first switch on the track diagram, true means connected
            isSecondTrackC:true, // the second switch on the track diagram, true means connected\
            trainX:null,
            trainY:null,
            trackPointNumber:0,
            isPreviousFinished: false,
            imgTrain: require('@/assets/train.jpg')
        }
    },
    methods:{
        doTrack1(train, trainSpeed, callback){
            let timer = null
            timer = setInterval(function(){
                if(Math.abs(1055-train.offsetLeft)<Math.abs(trainSpeed)){
                    clearInterval(timer)
                    this.trackPointNumber = 1
                    train.style.left = 1055 + 'px'
                    callback(train, trainSpeed)
                } else {
                    train.style.left = train.offsetLeft + trainSpeed + 'px'
                }
            },20)
        },
        doTrack2(train, trainSpeed){
            let timer = null
            let that = this
            timer = setInterval(function(){
                if(Math.abs(680-train.offsetTop)<Math.abs(trainSpeed)){
                    clearInterval(timer)
                    this.trackPointNumber = 2
                    train.style.top = 680 + 'px'
                    if(that.isFirstTrackC === true){
                        that.doTrack4(train, trainSpeed, that.doTrack5)
                    } else {
                        that.doTrack3(train, trainSpeed)
                    }
                } else {
                    train.style.top = train.offsetTop - trainSpeed + 'px'
                }
            },20)
        },
        doTrack3(TrainNum,TrainSpeed){
            let timer = null
            timer = setInterval(function(){
                if(Math.abs(110-TrainNum.offsetLeft)<Math.abs(TrainSpeed)){
                    // end track 3
                    clearInterval(timer)
                    this.trackPointNumber = 3
                    TrainNum.style.left = 110 + 'px'
                } else {
                    TrainNum.style.left = TrainNum.offsetLeft - TrainSpeed + 'px'
                }
            },20)
        },
        doTrack4(train, trainSpeed, callback){
            let that = this
            let timer = null
            timer = setInterval(function(){
                if(Math.abs(325-train.offsetTop)<Math.abs(trainSpeed)){
                    clearInterval(timer)
                    that.trackPointNumber = 4
                    train.style.top = 325 + 'px'
                    callback(train, trainSpeed, that.doTrack5)
                } else {
                    train.style.top = train.offsetTop - trainSpeed + 'px'
                }
            },20)
        },
        doTrack5(train, trainSpeed, callback){
            let that = this
            let timer = null
            timer = setInterval(function(){
                if(Math.abs(645-train.offsetLeft)<Math.abs(trainSpeed)){
                    clearInterval(timer)
                    this.trackPointNumber = 5
                    train.style.left = 645 + 'px'
                    callback(train, trainSpeed, that.doTrack6)
                } else {
                    train.style.left = train.offsetLeft - trainSpeed + 'px'
                }
            },20)
        },
        doTrack6(train, trainSpeed, callback){
            let that = this
            let timer = null
            timer = setInterval(function(){
                if(Math.abs(600-train.offsetTop)<Math.abs(trainSpeed)){
                    clearInterval(timer)
                    this.trackPointNumber = 6
                    train.style.top = 600 + 'px'
                    if(that.isSecondTrackC === true){
                        callback(train, trainSpeed, that.doTrack8)
                    } else {
                        callback(train, trainSpeed, that.doTrack7)
                    }
                    
                } else {
                    train.style.top = train.offsetTop + trainSpeed + 'px'
                }
            },20)
        },
        doTrack7(train, trainSpeed, callback){
            let that = this
            let timer = null
            timer = setInterval(function(){
                if(Math.abs(1055-train.offsetLeft)<Math.abs(trainSpeed)){
                    clearInterval(timer)
                    this.trackPointNumber = 7
                    train.style.left = 1055 + 'px'
                    callback(train, trainSpeed, that.doTrack4)
                } else {
                    train.style.left = train.offsetLeft + trainSpeed + 'px'
                }
            },20)
        },
        doTrack8(train, trainSpeed){
            let that = this
            let timer = null
            timer = setInterval(function(){
                if(Math.abs(680-train.offsetTop)<Math.abs(trainSpeed)){
                    clearInterval(timer)
                    that.trackPointNumber = 8
                    train.style.top = 680 + 'px'
                    that.doTrack9(train, trainSpeed)
                } else {
                    train.style.top = train.offsetTop + trainSpeed + 'px'
                }
            },20)
        },
        doTrack9(train, trainSpeed){
            let that = this
            let timer = null
            timer = setInterval(function(){
                if(Math.abs(110-train.offsetLeft)<Math.abs(trainSpeed)){
                    clearInterval(timer)
                    that.trackPointNumber = 9
                    train.style.left = 110 + 'px'
                } else {
                    train.style.left = train.offsetLeft - trainSpeed + 'px'
                }
            },20)
        },
        changeTrackDiagram(){
            if(this.isFirstTrackC === true && this.isSecondTrackC === true) this.trackDiagramChoose = cc;
            if(this.isFirstTrackC === true && this.isSecondTrackC === false) this.trackDiagramChoose = cdc;
            if(this.isFirstTrackC === false && this.isSecondTrackC === true) this.trackDiagramChoose = dcc;
            if(this.isFirstTrackC === false && this.isSecondTrackC === false) this.trackDiagramChoose = dcdc;
        },
        firstSwitch(){
            if(this.isFirstTrackC === true) this.isFirstTrackC = false;
            else this.isFirstTrackC = true;
            // change the track diagram after giving value to the switches
            this.changeTrackDiagram()
        },
        secondSwitch(){
            if(this.isSecondTrackC === true) this.isSecondTrackC = false;
            else this.isSecondTrackC = true;
            this.changeTrackDiagram()
        },
        move(e){
            this.trainX = e.pageX
            this.trainY = e.pageY
        },
        startMove(){
            let that = this
            let trainSpeed = 10
            let trainList = ['a','b','c','d','e']
            let waitForStart = trainList
            // random the list
            this.Remove()
            for(let i =0;i<trainList.length;i++){
                this.addTrain(trainList[i])
            }
            
            // here!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
            // start 5 trans from station
            // async function sleep (time) {
            //     return new Promise((resolve) => setTimeout(resolve, time));
            // }
            // while (waitForStart.length > 0){
                
            // }
            for(let i = 0; i<trainList.length;i++){
                (function(i){
                    setTimeout(function(){
                        console.log('Trains on the map: ' + (i + 1))
                        that.startTrain(trainList[i],trainSpeed)
                    },(i+1)*1000)
                })(i)
            }
        },
        Remove(){
            const trackDiagramDiv = document.getElementById("trackDiagram")
            let trackDiagramDivChild = trackDiagramDiv.children
            while(trackDiagramDivChild.length > 6){
                for(let index = 0; index<trackDiagramDivChild.length;index++){
                    if(trackDiagramDivChild[index].getAttribute("class")==="train"){
                        trackDiagramDiv.removeChild(trackDiagramDivChild[index])
                    }
                }
            }
            console.log('Trains on the map: ' + (trackDiagramDivChild.length - 6))
        },
        addTrain(trainName){
            const trackDiagramDiv = document.getElementById("trackDiagram")
            let newTrain = document.createElement("img")
            newTrain.setAttribute("class","train")
            newTrain.setAttribute("id",trainName)            
            newTrain.setAttribute("width","70px")
            newTrain.setAttribute("height","70px")
            newTrain.setAttribute("src",this.imgTrain)
            trackDiagramDiv.appendChild(newTrain)
        },
        startTrain(trainName, trainSpeed){
            let train = document.getElementById(trainName)
            this.doTrack1(train, trainSpeed, this.doTrack2)
        }
    }
}
</script>

<style>
#trackDiagram button{
    position: absolute;
    width:100px;
    height:50px;
}
#firstSwitch{
    left:90%;
    top:70%;
}
#secondSwitch{
    left:40%;
    top:65%;
}
#startMove{
    left:10%;
    top:25%;
}
#Remove{
    left:20%;
    top:25%;
}
.train{
    position: absolute;
    left:10%;
    top:100%;
}
#trackDiagram p{
    position:absolute;
    top:20%;
}
</style>