    <div class='header' id='mainHeader'>
        <h1>欢迎来到我的网页&emsp;&emsp;&emsp;CHEN QIN</h1>
    </div>
    <div class = 'beginningBody'>
        <div class = 'mypicture'>
            <img src = '../assets/myphoto.png' width=300 height=400>
        </div>
        <div>
            <p class = "beginningWords">&emsp;&emsp;{{beginningWordsRight[beginningWordsOpNumber]}}</p>
            <p class = 'beginningWords'>{{beginningWordsLeft[beginningWordsOpNumber]}}</p>
        </div>
        <div id='changeWordsButton'>
            <img src='../assets/Refresh.png' @click='changeWords()' width=50 height=50>
        </div>
    </div>
    <div class='movebarBody'>
        <div class = 'coverLeft'></div>
        <div class = 'coverRight'></div>
        <div class = 'movebar'>
            <p class = 'question'>那么，首先能告诉我你的年龄吗&emsp;&emsp;&emsp; 是24岁</p>
            <p class = 'question'>24岁......已经工作了吗&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;是学生</p>
            <p class = 'question'>那么身高体重是多少呢&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;身高是175，体重是50kg</p>
            <p class = 'question'>现在有在做什么运动吗&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;倒是有在健身</p>
        </div>
    </div>
    <div class='introVideoBody'>
        <iframe class="markdown-player" 
            src="//player.bilibili.com/player.html?aid=582964345&bvid=BV1J64y1M7Zd&cid=183469275&page=1" 
            width="100%" height="700" scrolling="no" border="0" frameborder="no" framespacing="0" allowfullscreen="true">
        </iframe>
    </div>
</div>

</template>

<script>
export default{
    name: 'Main',
    data(){
        return{
            beginningWordsOpNumber:0,
            beginningWordsLeft:["不受火冀望的比比皆是","世界始于无知","大啖食粮之刻已到"],
            beginningWordsRight:["因此你毋须畏惧黑暗","而终归于无","大啖食粮之刻已到"],
        }
    },
    mounted(){
    },
    methods:{
        // Change the beginning words
        changeWords(){
            var size = this.beginningWordsLeft.length
            if(this.beginningWordsOpNumber === size-1){
                this.beginningWordsOpNumber = 0
            } else {
                this.beginningWordsOpNumber++
            }
        },
    }
}
</script>


<style>
