.movebarBody{
    margin-left:-10px;
}
.coverLeft{
    position: absolute;
    z-index:2;
    width: 100px;
    height: 450px;
    margin-left:0px;
    background-color: white;
    display: block;
}
.coverRight{
    position: absolute;
    z-index:2;
