<template>
    <div
    :style="{position:'absolute', left:'10%', herf:'#'}"
    >
        <div 
        class = "gameTitleDiv"
        :style="{margin:'0px auto 25px auto'}"
        >
            <h2> {{gameTitle}}</h2>
            <h3 :style="{margin: '-10px auto 10px 100px'}"> {{gameSubTitle}} </h3>
            <button @click='initialize(mapSizeSelected,startFoodCountSelected,snakeSpeedSelected)'>开始</button>
            <button @click='stopAll()'>设置</button>
            <button @click='vip()'>购买皮肤</button>
            <audio src='../assets/laugh.wav'></audio>
        </div>
        <div 
        class = "game"
        id = "gameStart" 
        :style="{width:gameMapSize * 30 + 'px', height:gameMapSize * 30 + 'px'}"
        >
            <div 
            class = "grid" 
            v-for="(ele, index) in gridContainer" 
            :key="index"
            :style="{width: '28px', height: '28px', border: '1px solid black', float: 'left'}"
            >
                <div 
                :class="{
                    'snakeHead': ele.type == 'snakeHead',
                    'snakeBody' : ele.type == 'snakeBody',
                    'snakeFood' : ele.type == 'snakeFood'}"
                >
                </div>
            </div>
        </div>
        <div
        class = "game" 
        id = 'gameOver'>
            <select v-model="mapSizeSelected">
                <option v-for="(option, index) in mapSizeOptions" :value="option.value" :key='index'>
                {{ option.text }}
                </option>
            </select>
            <span :style="{padding:'10px'}">地图大小：{{ mapSizeSelected }}</span>
            <br />
            <br />
            <select v-model="startFoodCountSelected">
                <option v-for="(option, index) in startFoodCountOptions" :value="option.value" :key='index'>
                {{ option.text }}
                </option>
            </select>
            <span :style="{padding:'10px'}">地图食物数量：{{ startFoodCountSelected }}</span>
            <br />
            <br />
            <select v-model="snakeSpeedSelected">
                <option v-for="(option, index) in snakeSpeedOptions" :value="option.value" :key='index'>
                {{ option.text }}
                </option>
            </select>
            <span :style="{padding:'10px'}">移动速度：{{ snakeSpeedSelected }} (数字越大越慢)</span>
            <br />
            <br />
            <h3> 贪吃蛇 简介 </h3>
            <p>暂不支持移动端游玩，键盘方向键↑↓←→控制蛇的移动，当蛇碰触到自己身体时，游戏结束</p>
            <p>可根据自己的喜好设置游戏</p>
            <p>游戏存在<span :style="{color:'red',}">声音</span>,请确认带上耳机或在合适场合游玩</p>
            <p>各类皮肤火热上架中！欲购从速！</p>
            <p>点击顶端开始按钮进行游戏，点击设置按钮回到此页面</p>
        </div>
    </div>
</template>

<script>
export default {
    name: "Game",
    data() {
        return {
            gameTitle: "贪！吃！蛇！！",
            gameSubTitle: "ver 1.0",
            gameMapSize: 20,
            gridContainer: [],
            snakeHead:[],
            snakeBody:[],
            snakeBodyPop:[],
            snakeFood:[],
            snakeSpeed: 150,
            direction:"right",
            intervalSnake:'',
            gameOver:false,
            //
            mapSizeSelected: "12",
            mapSizeOptions:[{text:'小型', value:"8"},{text:'中型', value:"12"},{text:'大型', value:"20"},],
            startFoodCountSelected: "5",
            startFoodCountOptions:[{text:'极少', value:'1'},{text:'少量', value:'3'},{text:'中量', value:'5'},{text:'大量', value:'10'}],
            snakeSpeedSelected: "200",
            snakeSpeedOptions:[{text:'超慢', value:"1000"},{text:'慢速', value:"500"},{text:'中等', value:"200"},{text:'快速', value:"100"}]
        };
    },
    watch: {
        snakeHead:{
            handler(newVal){
                this.gridContainer.forEach((ele)=>{
                    if(ele.x === newVal[0] && ele.y === newVal[1]) ele.type = 'snakeHead';
                });
                this.snakeBody.forEach((body)=>{
                    if(body[0] === newVal[0] && body[1] === newVal[1]) {
                        this.gameOver = true;
                        this.makeSound();
                    }
                });
                this.snakeFood.forEach((food)=>{
                    if(food[0] === newVal[0] && food[1] === newVal[1]) {
                        this.snakeBody.push([this.snakeBody[this.snakeBody.length -1][0],this.snakeBody[this.snakeBody.length -1][1]]);
                        this.snakeFood.splice(this.snakeFood.indexOf(food), 1);
                        this.randomFoodPosition(1);
                    }
                });
            },
            immediate : true,
        },
        snakeBody:{
            handler(newVal){
                this.gridContainer.forEach((grid)=>{
                    newVal.forEach((body) =>{
                        if(grid.x === body[0] && grid.y === body[1]) grid.type = 'snakeBody';
                    })
                })
            },
            immediate : true,
        },
        snakeBodyPop:{
            handler(newVal){
                this.gridContainer.forEach((ele)=>{
                    if(ele.x === newVal[0] && ele.y === newVal[1]) ele.type = 'blank';
                })
            },
            immediate : true,
        },
        /**
         * Watch on snakeFood
         * It mainly check whether the new randomly generated food is on the snake's body
         * If so, randomly generate food again;
         * Otherwise, define that food grid as 'snakeFood'
         */
        snakeFood:{
            handler(newVal){
                newVal.forEach((food)=>{
                    this.snakeBody.forEach((body)=>{
                        if(food[0] === body[0] && food[1] === body[1]) {
                            this.snakeFood.splice(this.snakeFood.indexOf(food), 1);
                            this.randomFoodPosition(1);
                        }
                    })
                });
                this.gridContainer.forEach((grid)=>{
                    newVal.forEach((food) =>{
                        if(grid.x === food[0] && grid.y === food[1]) grid.type = 'snakeFood';
                    })
                });
            },
            immediate : true,
        },
        gameOver:{
            handler(newVal){
                if(newVal) {
                    this.stopAll();
                    this.gameOver = false;
                    document.getElementById("gameOver").style.display = "block";
                    document.getElementById("gameStart").style.display = "none";
                }
            },
            immediate : true,
        }
    },
    mounted() {
        this.keyInput();
    },
    methods: {
        initialize(mapSize, foodCount, moveSpeed){
            this.gameMapSize = mapSize;
            this.stopAll();
            this.addingGrid();
            this.initializeSnakePosition();
            this.randomFoodPosition(foodCount);
            this.startMove(moveSpeed);
            document.getElementById("gameOver").style.display = "none";
            document.getElementById("gameStart").style.display = "block";
        },
        addingGrid(){
            for(let i = 0; i < this.gameMapSize; i++){
                for(let j = 0; j < this.gameMapSize; j++){
                    this.gridContainer.push({x: i, y: j, type: 'blank'});
                }
            }
        },
        initializeSnakePosition(){
            this.snakeHead = [2,2];
            this.snakeBody.push([2,1],[2,0]);
        },
        randomFoodPosition(num){
            if(num === 0) return; 
            let foodPosition = [this.randomNumber(this.gameMapSize), this.randomNumber(this.gameMapSize)];
            // if(foodPosition === this.snakeHead) this.randomFoodPosition(num);
            // this.snakeBody.forEach((body)=>{
            //     if(body === foodPosition) this.randomFoodPosition(num);
            // })
            this.snakeFood.push(foodPosition);
            this.randomFoodPosition(num - 1);
        },
        keyInput(event){
            let that = this;
            document.onkeydown = function(e) {
                let e1 = e || event || window.event || arguments.callee.caller.arguments[0]
                if (e1 && e1.keyCode == 37) {
                    console.log("←");
                    if(that.direction !== "right") that.direction = "left";
                } else if (e1 && e1.keyCode == 38) {
                    console.log("↑");
                    if(that.direction !== "down") that.direction = "up";
                } else if (e1 && e1.keyCode == 39) {
                    console.log("→");
                    if(that.direction !== "left") that.direction = "right";
                } else if (e1 && e1.keyCode == 40) {
                    console.log("↓");
                    if(that.direction !== "up") that.direction = "down";
                }
            }
        },
        startMove(speed){
            let that = this;
            clearInterval(this.intervalSnake);
            this.intervalSnake = setInterval(()=>{
                that.snakeBody.unshift(that.snakeHead);
                that.snakeBodyPop = that.snakeBody.pop();
                if(that.direction === "right"){
                    that.snakeHead = that.snakeHead[1] === that.gameMapSize-1 ? [that.snakeHead[0],0] : [that.snakeHead[0],that.snakeHead[1]+1];
                } else if(that.direction === 'left'){
                    that.snakeHead = that.snakeHead[1]-1 === -1 ? [that.snakeHead[0],that.gameMapSize -1] : [that.snakeHead[0],that.snakeHead[1]-1];
                } else if(that.direction === 'up'){
                    that.snakeHead = that.snakeHead[0]-1 === -1 ? [that.gameMapSize - 1,that.snakeHead[1]] : [that.snakeHead[0]-1,that.snakeHead[1]];
                } else if(that.direction === 'down'){
                    that.snakeHead = that.snakeHead[0] === that.gameMapSize-1 ? [0,that.snakeHead[1]] : [that.snakeHead[0]+1,that.snakeHead[1]];
                }
            },speed);
        },
        randomNumber(input){
            return Math.floor(Math.random()*input);
        },
        displayAllGrid(){
            for(const [key,value] of Object.entries(this.gridContainer)){
                console.log(key + ': ' + value);
            }
        },
        stopAll(){
            this.gridContainer = [];
            this.snakeHead=[];
            this.snakeBody=[];
            this.snakeBodyPop = [];
            this.snakeFood=[];
            this.snakeSpeed = 150;
            this.gameOver=false;
            this.direction = "right";
            clearInterval(this.intervalSnake);
            document.getElementById("gameOver").style.display = "block";
            document.getElementById("gameStart").style.display = "none";
        },
        makeSound(){
            var playPromise = document.querySelector('audio').play();
            // In browsers that don’t yet support this functionality,
            // playPromise won’t be defined.
            if (playPromise !== undefined) {
                playPromise.then(function() {
                    // Automatic playback started!
                }).catch(function(error) {
                    // Automatic playback failed.
                    // Show a UI element to let the user manually start playback.
                });
            }
        },
        vip(){
            alert("尽情期待!");
        }
        
    },
};
</script>


<style scoped>


.game{
    position: absolute;

}
#gameStart{
    display:none;
}
#gameOver{
    display:block;
}

.gameTitleDiv{
    position: relative;
    left:200px;
    width:800px;
}
.snakeHead{
    background-color: #009900;
    width: 100%;
    height: 100%;
}
.snakeBody{
    background-color: #00FF00;
    width: 100%;
    height: 100%;
}
.snakeFood{
    background-color: #000000;
    width: 100%;
    height: 100%;
}
</style>